*,*::before,*::after{
    box-sizing: border-box;
  }

  img{
    max-width: 100%;
    vertical-align: middle;
    height: auto;
  }
  .container{
    max-width: 1110px;
    margin: 0 auto;
  }

  .d-flex{
      display: flex;
  }

  .header {
    background-color: $primary;
    padding: 30px 88px;
    justify-content: space-between;
  }

  .navbar{
      justify-content: space-around;
      color: #fff;
  }
  
  .footer {
    height: 200px;
    background-color: #00cc99;
  }
  
  .box {
    width: 100px;
    height: 100px;
    background-color: red;
    display: flex;
    transition: all 0.5s;
    &:hover {
      width: 200px;
      background-color: $primary;
    }
  }